@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    font-family: 'Poppins', sans-serif !important;
    background-color: rgba(203, 0, 0, 0.1);
}

.MuiTypography-body1 {
    font-family: 'Poppins', sans-serif;
}

.MuiButton-root {
    font-family: 'Poppins', sans-serif;
}

.MuiInputBase-root {
    font-family: 'Poppins', sans-serif;
}

.MuiMenuItem-root {
    font-family: 'Poppins', sans-serif;
}

.MuiSelect-select.MuiSelect-select {
    padding-left: 10px;
}

.MuiIconButton-root {
    /* background-color: #ccf7a2a1 !important;
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12); */
}

body::-webkit-scrollbar {
    width: 7px;
}

body::-webkit-scrollbar-track {
    background-color: rgb(83, 80, 75);
}

body::-webkit-scrollbar-thumb {
    background-color: rgb(138, 190, 214);
}

.todo-head {
    text-align: center;
    font-size: 22px;
    margin: 0;
    margin-top: 50px;
    margin-bottom: 0px;
}

.date .MuiIconButton-root {
    background-color: transparent !important;
    box-shadow: none;
}

.MuiPickersDay-day {
    background-color: transparent !important;
    box-shadow: none;
}

.MuiPickersDay-daySelected:hover {
    background-color: #3f51b5 !important;
}

.MuiPickersDay-daySelected {
    background-color: #3f51b5 !important;
}

.MuiPickersCalendarHeader-iconButton {
    background-color: #fff !important;
    box-shadow: none;
}

.MuiCheckbox-root {
    background-color: transparent !important;
    box-shadow: none;
}

@media (max-width: 768px) {
    .MuiIconButton-root {
        padding: 5px !important;
    }
    .MuiListItem-gutters {
        padding: 8px !important;
    }
}
.todo-main {
    width: 60%;
    margin: 20px auto 50px auto;
    padding: 20px;
    border-radius: 5px;
    background-color: rgb(254, 255, 255);
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.todo-list_item {
    /* background-color: rgba(109, 190, 196, 0.15); */
    background: linear-gradient(225deg, #CDD2FD 3.26%, #6979F8 100%);
    transition: all 0.5s;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.15), 0px -1px 3px 0px rgba(0, 0, 0, 0.15);
}

.todo-list_item-done {
    /* background-color: rgba(53, 245, 60, 0.61) !important; */
    background: linear-gradient(225deg, #aaf7bd 3.26%, #33db6b 100%);
}

.todo-list_item-done:hover {
    /* background-color: rgba(41, 204, 46, 0.693) !important; */
    /* background: linear-gradient(225deg, #aaf7bd 3.26%, #33db6b 100%); */
}

.todo-list_item-progress {
    background-color: rgba(71, 84, 253, 0.693) !important;
}

.todo-list_item-progress:hover {
    background-color: rgba(55, 65, 206, 0.693) !important;
}

.todo-list_item:hover {
    background-color: rgba(75, 141, 146, 0.15);
    box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.1), 0px -2px 10px 0px rgba(0, 0, 0, 0.1);
}

.status-todo {
    width: 20%;
    font-weight: 600;
}

.status-todo span {
    font-weight: 600;
}

.text-todo {
    width: 60%;
}

.text-todo span {
    font-weight: 600;
}

.w-100 {
    width: 100%;
}

.w-60 {
    width: 60%;
}

.mb-20 {
    margin-bottom: 20px;
}

.mt-20 {
    margin-top: 20px !important;
}

.mb-10 {
    margin-bottom: 10px;
}

.mr-15 {
    margin-right: 15px;
}

.d-flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.show-md {
    display: none;
}

@media (max-width: 768px) {
    .todo-main {
        width: 90%;
        padding: 10px;
    }
    .text-todo {
        width: 30%;
    }
    .f-14 {
        font-size: 14px !important;
    }
    .f-14 span {
        font-size: 14px !important;
    }
    .status-todo {
        width: 30%;
    }
    .hide-md {
        display: none;
    }
    .show-md {
        display: block;
    }
}
